.projects-p {
    margin-left: 100px;
    font-size: 1em;
    text-align: left;
    /* max-width: 400px; */
    color: white;
    margin-top: 40px;
    margin-bottom: 40px;
    line-height: 157.2%;
    margin-right: 100px;
}

.projects-h1 {
    font-size: 2.5em;
    text-align: left;
    margin-left: 100px;
    color: white;
    margin-top: 0px;
    line-height: 10px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}

@media screen and (max-width: 1024px) {
    .projects-h1 {
        font-size: 2.0em;
    }
}

@media screen and (max-width: 800px) {
    .projects-h1 {
        font-size: 1.5em;
    }
}

@media screen and (max-width: 500px) {
    .Projects {
        display: flex;
        flex-direction: column;
        /* justify-content: center;
        align-content: center; */
    }
    .projects-title hr {
        margin-left: 40px;
    }
    .projects-h1 {
        font-size: 1.2em;
        margin-left: 40px;
        margin-top: 16px;
    }
    .projects-p {
        display: none;
    }
    .projects-title h5 {
        font-size: 0.6em;
        margin-bottom: 0px;
        margin-left: 0px;
    }
    .projects-title {
        margin-bottom: 0;
    }
}
.Testimonials {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    max-width: 1500px;
    margin: 0 auto;
}

.TestimonialsLeft {
    display: flex;
    flex-direction: column;
    width: 55%;
    justify-content: center;
    align-content: center;
}

.TestimonialsLeft hr {
    width: 75px;
    border: 1px solid #E27D60;
    float: left;
    margin-left: 100px;
}

.testimonials-title {
    display: flex;
    flex-direction: row;
}

.testimonials-title h5 {
    color: #E27D60;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}

.TestimonialsLeft h1 {
    font-size: 2.5em;
    text-align: left;
    margin-left: 100px;
    color: white;
    margin-top: 0px;
    line-height: 10px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}

.sliderTestimonials {
    width: 600px;
    margin-left: 100px;
}

.TestimonialsRight {
    width: 45%;
    display: flex;
    justify-content: center;
    margin-right: 100px;
}

.TestimonialsRight img {
    width: 450px;
}

.slick-dots li button:before {
    /* color: orange; */
    color: black;
    font-size: 15px;
}

.slick-dots li.slick-active button:before {
    color: #E76F51 !important;
}

@media screen and (max-width: 1024px) {
    .TestimonialsLeft h1 {
        font-size: 2.0em;
    }
    .sliderTestimonials {
        max-width: 400px;
    }
    .TestimonialsRight img {
        max-width: 250px;
    }
}

@media screen and ( max-width: 800px) {
    .Testimonials {
        flex-direction: column-reverse;
    }
    .TestimonialsLeft {
        width: 100%;
    }
    .sliderTestimonials {
        max-width: 100%;
    }
    .TestimonialsLeft h1 {
        font-size: 1.5em;
    }
    .TestimonialsRight {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 50px;
    }
    .TestimonialsRight img {
        width: 300px;
    }
}

@media screen and (max-width: 500px) {
    .Testimonials {
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-content: center;
        margin: 0 auto;
    }
    .TestimonialsRight {
        grid-area: TestimonialsRight;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        margin-bottom: 50px;
        text-align: left;
    }
    .TestimonialsRight img {
        width: 200px;
    }
    .TestimonialsLeft {
        grid-area: TestimonialsLeft;
        display: flex;
        justify-content: center;
        margin: 0;
        width: 100%;
        /* margin-left: 20px; */
    }
    .TestimonialsLeft h1 {
        font-size: 1.2em;
        margin-left: 40px;
    }
    .TestimonialsLeft hr {
        margin-left: 40px;
    }
    .TestimonialsLeft h5 {
        font-size: 0.6em;
    }
    .sliderTestimonials {
        max-width: 100vw;
        margin-left: 0px;
        display: flex;
        justify-content: center;
    }
}
.CardTestimonial {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    text-align: left;
}

.CardTestimonial img {
    width: 45px;
    position: absolute;
}

.CardTestimonial p {
    text-align: left;
    color: white;
    font-size: 1em;
    margin-top: 40px;
    line-height: 157.2%;
    font-size: 1.1em;
    font-style: italic;
    text-align: justify;
    margin-right: 10px;
}

.CardTestimonial h4 {
    color: white;
    font-weight: 800;
    font-size: 1.5em;
}

.CardTestimonial h6 {
    color: white;
    font-weight: 300;
    font-size: 1.2em;
    margin-top: 0;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}

@media screen and (max-width: 500px) {
    .CardTestimonial {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        margin: 0 auto;
    }
    .CardTestimonial p {
        font-size: 13px;
        font-weight: 300;
        margin-left: 40px;
        line-height: 180%;
        margin-right: 40px;
    }
    .CardTestimonial h4 {
        margin-top: 5px;
        margin-bottom: 5px;
        font-size: 1em;
        margin-left: 40px;
    }
    .CardTestimonial h6 {
        margin-left: 40px;
        font-size: 1em;
    }
    .CardTestimonial img {
        width: 45px;
        bottom: 230px;
        left: 40px;
    }
}
.Mobile {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.5fr 0.5fr 0.5fr 0.5fr 1fr;
    grid-gap: 70px 0px;
    gap: 70px 0px;
    margin: 0;
    max-width: 100vw;
    grid-template-areas: "HomeGridContainer" "AboutGridContainer" "FeaturedProjectGridContainer" "ProjectsGridContainer" "TestimonialsGridContainer";
}

.HomeGridContainer {
    grid-area: HomeGridContainer;
}

.AboutGridContainer {
    grid-area: AboutGridContainer;
}

.FeaturedProjectGridContainer {
    grid-area: FeaturedProjectGridContainer;
}

.ProjectsGridContainer {
    grid-area: ProjectsGridContainer;
}

.TestimonialsGridContainer {
    grid-area: TestimonialsGridContainer;
}
